.weedesign-images2webp-admin-component {
    display:block;
    margin-bottom:20px;
    button {
        margin-right:10px;
    }
}
.weedesign-images2webp-admin-component-progress {
    p {
        line-height: 16px;
        font-size: 14px;
        margin-top:5px;
    }
}
.sw-system-config--field-weedesign-images-2-web-p-config-webp {
    display:none;
}
.sw-system-config--field-weedesign-images-2-web-p-config-weedesign-images-2-web-p-media-delete,
.sw-system-config--field-weedesign-images-2-web-p-config-weedesign-images-2-web-p-media-skip,
.sw-system-config--field-weedesign-images-2-web-p-config-weedesign-images-2-web-p-media-generate,
.sw-system-config--field-weedesign-images-2-web-p-config-weedesign-images-2-web-p-media-progress,
.sw-system-config--field-weedesign-images-2-web-p-config-weedesign-images-2-web-p-upgrade,
.sw-system-config--field-weedesign-images-2-web-p-config-weedesign-images-2-web-p-htaccess,
.sw-system-config--field-weedesign-images-2-web-p-config-weedesign-images-2-web-p-htaccess-info,
.sw-system-config--field-weedesign-images-2-web-p-config-preload {
    label {
        display:none;
    }
}
.sw-system-config--field-weedesign-images-2-web-p-config-weedesign-images-2-web-p-media-generate,
.sw-system-config--field-weedesign-images-2-web-p-config-weedesign-images-2-web-p-htaccess-info {
    .weedesign-images2webp-admin-info {
        margin: -38px -30px 40px;
        border-radius: 0;
        padding: 15px 25px;
    }
}
.sw-system-config--field-weedesign-images-2-web-p-config-weedesign-images-2-web-p-htaccess-info {
    .weedesign-images2webp-admin-info {
        margin-bottom:30px;
    }
}
.sw-system-config--field-weedesign-images-2-web-p-config-preload {
    p:last-child {
        margin:10px 0 0;
        font-size:14px;
    }
    button {
        margin:40px 0 10px;
    }
    .sw-field {
        margin-bottom:0;
    }
    .weedesign-images2webp-admin-info {
        margin: -38px -30px 0;
        border-radius: 0;
        padding: 15px 25px;
    }
}
.sales-channel-is-selected {
    .sw-system-config--field-weedesign-images-2-web-p-config-weedesign-images-2-web-p-media-generate,
    .sw-system-config--field-weedesign-images-2-web-p-config-weedesign-images-2-web-p-htaccess-info,
    .sw-system-config--field-weedesign-images-2-web-p-config-preload {
        .weedesign-images2webp-admin-info {
            margin-top:30px;
        }
    }
}
.weedesign-images2webp-admin-component-progress-alert {
    color:#c00;
}
.sw-system-config--field-weedesign-images-2-web-p-config-weedesign-images-2-web-p-media-options {
    padding-top:25px;
    border-top:1px solid #d1d9e0;
    label {
        font-weight:bold;
    }
}
.weedesign-images2webp-config-hide {
    display:none;
}
.weedesign-images2webp-config.sw-system-config {
    &.weedesign-images2webp-reload {
        display:none;
    }
    .sw-system-config__card--0 {
        .sw-inherit-wrapper__toggle-wrapper {
            display:none;
        }
        .sw-field--switch {
            display:none;
        }
        .sw-card__header {
            display:none;
        }
        .sw-card__content {
            padding:0;
            border-radius:8px;
            overflow: hidden;
        } 
    }
}
.weedesign-images2webp-admin-component-delete {
    button {
        border:1px solid #c00;
        background:rgba(204, 0, 0, .2);
    }
}
.weedesign-images2webp-admin-info {
    margin-top: 20px;
    font-size: 14px;
    border-radius:4px;
    background:rgba(255, 236, 204, .8);
    padding: 15px;
}
.weedesign-images2webp-admin-component-upgrade {
    .weedesign-images2webp-admin-component-upgrade-intro {
        background: #243758;
        color:#fff;
        padding: 20px 30px;
        font-size: 18px;
        font-weight: 600;
        line-height: 18px;
    }
    .weedesign-images2webp-admin-component-upgrade-status {
        margin:10px 30px 30px;
        font-size: 14px;
        span {
            font-weight:bold;
        }
        &.weedesign-images2webp-admin-component-upgrade-status-info {
            margin:30px;
        }
    }
    .sw-progress-bar {
        padding:0 30px;
    }
    .weedesign-images2webp-admin-info {
        margin:0 0 40px;
        padding:15px 30px;
    }
    button {
        margin:0 30px 5px;
    }
    .weedesign-images2webp-admin-component-upgrade-info {
        margin: 0 30px 40px;
        font-size: 12px;
    }
}
.weedesign-images2webp-media-upgrade-started {
    .sw-system-config__card--2 {
        position:relative;
        filter: blur(3px);
        &:before {
            content:"";
            position:absolute;
            top:0;
            left:0;
            right:0;
            bottom:0;
            cursor:wait;
            z-index:9;
        }
    }
}
.weedesign-images2webp-clear {
    clear:both;
}
.sw-media-sidebar .sw-media-sidebar__quickaction {
    .sw-media-sidebar__quickaction {
        margin-bottom:0;
    }
}
.quickaction--webp-title {
    display:none;
}
.weedesign-images2webp-config .sw-system-config__card--0 {
    display:none;
    &.sw-card--visible {
        display:block;
    }
}
